body {
  position: relative;
  height: 100%;
  width: 100%;
}
body .iucp-upsell-overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  opacity: 0;
  background-color: #fff;
  transition: all 0.2s;
}
body .iucp-upsell-overlay.active {
  opacity: 0.88;
}

.iucp-products-container > .glide.iucp-upsell-products-container {
  width: 50%;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 999999;
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s;
}
.iucp-products-container > .glide.iucp-upsell-products-container.active {
  opacity: 1;
  visibility: visible;
}

.glide .glide__arrow--right.iucp {
  right: -3%;
}
.glide .glide__arrow--left.iucp {
  left: -3%;
}

.lds-dual-ring {
  display: inline-block;
}
.lds-dual-ring.hidden {
  display: none;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 1.3rem;
  height: 1.3rem;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.hidden {
  display: none;
}

